@import url("https://cdn.jsdelivr.net/gh/orioncactus/pretendard@v1.3.9/dist/web/variable/pretendardvariable.min.css");

body {
  font-family: "Pretendard Variable", Pretendard, -apple-system, BlinkMacSystemFont, system-ui, Roboto, "Helvetica Neue", "Segoe UI", "Apple SD Gothic Neo", "Noto Sans KR", "Malgun Gothic", "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol", sans-serif;

}

.Project {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: start;
  font-family: "Pretendard Variable", Pretendard, -apple-system, BlinkMacSystemFont, system-ui, Roboto, "Helvetica Neue", "Segoe UI", "Apple SD Gothic Neo", "Noto Sans KR", "Malgun Gothic", "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol", sans-serif;
}

.Project_slide {
  position: relative;
  width: 1200px;
  height: 400px;
  overflow: hidden;
}

.slide_1 {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  opacity: 0;
  transition: opacity 1s ease-in-out;
  object-fit: cover;
}

.slide_1.active {
  opacity: 1;
}

.Project_1 {
  display: inline-block;
  position: relative;
  margin-left: 100px;
  text-align: left;
  font-family: "Pretendard Variable", Pretendard, -apple-system, BlinkMacSystemFont, system-ui, Roboto, "Helvetica Neue", "Segoe UI", "Apple SD Gothic Neo", "Noto Sans KR", "Malgun Gothic", "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol", sans-serif;
  font-size: 40px;
  font-weight: 200;
}

.Project_1::before {
  content: '';
  position: absolute;
  left: 0;
  right: 0;
  bottom: 0.1em;
  height: 0.8em;
  /* 형광펜 두께 */
  background: linear-gradient(90deg, #6bab4c, #338b41);
  z-index: -1;
  opacity: 0.5;
  /* 형광펜 느낌을 위해 투명도 조절 */
}


.ImageList {
  width: 100%;
  
  overflow-x: auto;
  
}

.ImageCard {  
  display: flex;
  margin: 0px;
  background-size: 100%;
}


.textContainer {
  font-family: "Pretendard Variable", Pretendard, -apple-system, BlinkMacSystemFont, system-ui, Roboto, "Helvetica Neue", "Segoe UI", "Apple SD Gothic Neo", "Noto Sans KR", "Malgun Gothic", "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol", sans-serif;
}

.textContainer p {
  font-family: "Pretendard Variable", Pretendard, -apple-system, BlinkMacSystemFont, system-ui, Roboto, "Helvetica Neue", "Segoe UI", "Apple SD Gothic Neo", "Noto Sans KR", "Malgun Gothic", "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol", sans-serif;
  font-weight: 700;
  font-size: 20px;
  margin-bottom: 0;
}

.textContainer a {
  margin-top: 0px;
  font-weight: 200px;
  
  font-size: 15px;
}


.project_container {
  overflow: hidden;
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding: 100px;
  width: 100%;
  height: 700px;
  align-items: center;
}
.project_container p {
  text-align: left;
  width: 100%;
  font-size: 30px;
  font-weight: 100;
}
.project_imgbox {
  align-items: center ;
  justify-content: center;
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: row;
}
.project_imgbox2 {
  display: flex;
  width: 1000px;
  justify-content: flex-start;
  gap: 20px;
}

.bath_part {
  display: flex;
  flex-direction: column;
  width: 100%;
  text-align: left;
  font-weight: 200;
  color : black
}
.bath_part p {
  margin : 0;
}
/* 모바일 대응 */
@media (max-width : 768px) {
body {
  font-size: 15px;
}
  .Project {
  width: 100%;
  font-size: 18px;
  }
  .Project_1{
    font-size: 23px;
    margin-left: 10px;
  }
  .project_container {
    height: 350px;
    margin-top: 0;
    padding: 5px;
  }
  .project_container p {
    font-size: 25px;
    font-weight: 300;
  }
  .project_imgbox {
    width: 100%;
    height: 150px;
    
  }
  .project_imgbox2 {
    width: 100%;
    height: 1000px;
    gap: 0;
    
  }
  .project_imgbox2 p {
    font-size: 20px;
  }

  

}